.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.avatar{
 border-radius: 50%;
}

.spacer{
  padding-top: 40px;
  margin-bottom: 40px;
  border-bottom: 1px lightgray dashed ;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 90px;
  font-weight: bold;
  line-height: 80px;
  height: 100px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 100px;
}

.game {
 width: 1000px;
 margin: 0 auto;
}

.game h1{
  text-align: center;
}

.game .status {
  margin-bottom: 10px;
  font-size: 21px;
  font-weight: 600;
  text-align: center;
}

.game .board-row:after {
  clear: both;
  content: '';
  display: table;
}

.game .board{
  width: 300px;
  margin: auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
